<style>
.m-tongji-recive {
  padding: 20px;
}
.m-tongji-recive-header {
  margin-bottom: 20px;
}
.m-tongji-recive .el-table td{
  padding: 5px 0;
}
.m-tongji-recive .el-input-group__prepend{
  width: 50px;
}
.m-tongji-recive-dialog .el-input {
  width: 300px;
}
.m-tongji-recive-dialog .number-input .el-input {
  width: 100%;
}
.m-tongji-recive-dialog .el-form {
  padding: 20px;
}
</style>
<template>
  <div class="m-tongji-recive">
    <el-button class="add_btn" type="primary" @click="onEditEvent('add')">新增收款信息</el-button>
    <div class="search" style="margin: 15px 0;width: 300px;display:inline-block;">
      <el-input placeholder="请输入搜索内容" v-model="keyword" class="input-with-select">
        <el-select v-model="selected" slot="prepend" placeholder="请选择">
          <el-option label="客户姓名" value="customer_name"></el-option>
          <el-option label="客户编号" value="customer_number"></el-option>
        </el-select>
        <el-button @click="handleSearch" slot="append" icon="el-icon-search"></el-button>
      </el-input>
    </div>
    <el-table
      v-loading="loading"
      :data="rows_inShow"
      stripe
      style="width: 100%">
      <el-table-column
        v-for="col in cols"
        :key="col.prop"
        :prop="col.prop"
        :label="col.label"
        :formatter="col.formatter"
        :width="col.width">
      </el-table-column>
      <el-table-column
        label="操作"
        width="100">
        <template slot-scope="scope">
          <el-button
            @click.native.prevent="onEditEvent(scope.$index)"
            type="text">
            编辑
          </el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
      style="margin: 20px 0;text-align:center;"
      background
      :page-size="page_size"
      :total="total"
      layout="prev, pager, next"
      :current-page="page"
      @current-change="handlePageChange">
    </el-pagination>
    <el-dialog class="m-tongji-recive-dialog" append-to-body :title="(isEdit?'编辑':'新增')+'收款信息'" :visible.sync="showEditDialog">
      <el-form :model="item" :rules="rules" ref="itemForm" label-position="right" label-width="120px">
        <el-form-item label="客户编号">
          <strong>{{ this.item.customer_number }}</strong>
        </el-form-item>
        <el-form-item label="客户姓名" prop="customer_name">
          <el-input v-model="item.customer_name" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item class="number-input" label="收款金额(本金)" prop="receive_money">
          <el-input-number v-model="item.receive_money" controls-position="right"></el-input-number>
        </el-form-item>
        <el-form-item class="number-input" label="收款(利息)" prop="receive_interest">
          <el-input-number v-model="item.receive_interest" controls-position="right"></el-input-number>
        </el-form-item>
        <el-form-item class="number-input" label="收款(其他)" prop="receive_other">
          <el-input-number v-model="item.receive_other" controls-position="right"></el-input-number>
        </el-form-item>
        <el-form-item label="收款日期" prop="reveive_date">
          <el-date-picker type="date" placeholder="选择日期" value-format="yyyy-MM-dd" v-model="item.reveive_date" />
        </el-form-item>
        <el-form-item label="备注">
          <el-input type="textarea" :rows="3" placeholder="请输入内容" v-model="item.commit" auto-complete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="showEditDialog = false">取 消</el-button>
        <el-button type="primary" @click="onDialogComfirm">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
import { rest } from '@/common/request';
import { currencyDisplay } from '@/common/utils'

export default {
  data(){
    return {
      cols:[
        {
          prop: 'customer_number',
          label: '客户编号'
        },{
          prop: 'customer_name',
          label: '姓名'
        },{
          prop: 'receive_money',
          label: '收款金额(本金)',
          formatter:(r,c,v)=>{
            return currencyDisplay(v);
          },
          width: 130
        },{
          prop: 'receive_interest',
          label: '收款(利息)',
          formatter:(r,c,v)=>{
            return currencyDisplay(v);
          },
          width: 130
        },{
          prop: 'receive_other',
          label: '收款(其他) ',
          formatter:(r,c,v)=>{
            return currencyDisplay(v);
          },
          width: 130
        },{
          prop: 'reveive_date',
          label: '收款日期',
          width: 130
        },{
          prop: 'commit',
          label: '摘要'
        }
      ],
      rows: [],
      rules: {
        customer_name:[
          {required: true, message: '请输入客户姓名', trigger: 'blur'}
        ],
        receive_money:[
          {type: 'number', required: true, message: '请输入收款金额(本金)', trigger: 'blur'}
        ],
        receive_interest:[
          {type: 'number', required: true, message: '请输入收款(利息)', trigger: 'blur'}
        ],
        receive_other:[
          {type: 'number', required: true, message: '请输入收款(其他)', trigger: 'blur'}
        ],
        reveive_date:[
          {required: true, message: '请输入收款日期', trigger: 'change'}
        ]
      },
      loading: true,

      item: {},
      isEdit: true,
      showEditDialog: false,
      
      page: 1,
      page_size: 10,
      total: 0,
      search: {
        name: '',
        customer_number: '',
        phone: ''
      },
      keyword: '',
      selected: null
    }
  },
  computed:{
    rows_inShow(){
      let arr = [];
      this.rows.forEach(item => {
        let obj = {}
        for(let key in item){
          if(key === 'car_status'){
            obj[key] = item[key]?'押车':'拖车';
          }else if(key === 'borrow_status'){
            obj[key] = item[key]?'已被借用':'可以借用';
          }else{
           obj[key] = item[key]
          }
        }
        arr.push(obj)
      })
      return arr;
    }
  },
  methods:{
    fetchList(data = {
      page: this.page,
      page_size: this.page_size,
      customer_number: this.search.customer_number,
      customer_name: this.search.customer_name
    }){
      let uid = sessionStorage.getItem('ts_user_id');
      return rest({
        url: '/v1/inspect/'+ uid +'/recive',
        headers:{
          'X-Inspect-Token': sessionStorage.getItem('ts_userToken')
        },
        method: 'POST',
        data: data
      }).then(res => {
        this.rows = res.data.data;
        this.total = res.data.total;
        this.loading = false;
      }).catch(res => {
        this.$message.error(res.response.data.msg);
      });
    },
    updateItem(data){
      let uid = sessionStorage.getItem('ts_user_id');
      return rest({
        url: '/v1/inspect/'+ uid +'/recive/update',
        headers:{
          'X-Inspect-Token': sessionStorage.getItem('ts_userToken')
        },
        method: 'POST',
        data: data
      }).then(res => {
        this.$message({
          message: '编辑收款信息成功，请在列表中查看！',
          type: 'success'
        });
        this.fetchList();
        this.showEditDialog = false;
      }).catch(res => {
        this.$message.error(res.response.data.msg);
      })
    },
    handlePageChange(val){
      this.page = val;
      this.fetchList();
    },
    handleSearch(){
      if(!this.selected){
        this.$message('请先选择搜索类型！');
        return;
      }
      this.search[this.selected] = this.keyword;
      if(this.page == 1){
        this.fetchList();
      }else{
        this.page = 1;
      }
    },
    onEditEvent(index){
      if(index === 'add'){
        this.isEdit = false;
        this.item = {
          customer_number: this.rows[index].customer_number,
          customer_name: this.rows[index].customer_name,
          commit: this.rows[index].commit,
          receive_money: this.rows[index].receive_money,
          receive_interest: this.rows[index].receive_interest,
          receive_other: this.rows[index].receive_other,
          reveive_date: this.rows[index].reveive_date
        }
      }else{
        this.isEdit = true;
      }
      this.item = {
        receive_money_id: this.rows[index].receive_money_id,
        customer_number: this.rows[index].customer_number,
        customer_name: this.rows[index].customer_name,
        commit: this.rows[index].commit,
        receive_money: this.rows[index].receive_money,
        receive_interest: this.rows[index].receive_interest,
        receive_other: this.rows[index].receive_other,
        reveive_date: this.rows[index].reveive_date
      }
      this.isEdit = true;
      this.showEditDialog = true;
      this.$refs['itemForm']?this.$refs['itemForm'].clearValidate():null;
    },
    onDialogComfirm(){
      this.$refs['itemForm'].validate((valid) => {
        if(valid){
          this.updateItem(this.item);
        }else{
          return false;
        }
      });
    }
  },
  mounted(){
    this.fetchList();
  }
}
</script>




